import SimpleToolTip from "components/SimpleToolTip/SimpleToolTip";
import { withAuth } from "components/hoc/withAuth";
import { FileUpload } from "primereact/fileupload";
import { INPUT_STYLE_TYPE, VARIANTS } from "utils/Constant";

const FileUploadBox = (props: any) => {
  const chooseOptionsPrimary = {
    className: "border-none text-xs  text-primary-ptext-1000 cursor-pointer",
    label: props.label || "Upload",
    icon: "pi pi-plus hover:border-1.5  hover:border-primary-sapphireBlue-600  text-primary-sapphireBlue-600  shadow-iconButton  bg-white  border border-1.5 border-primary-sapphireBlue-600  focus:white  focus:border focus:border-semantic-informativeAccent  hover:bg-primary-tableRowColor  rounded-full  text-xs  px-0  py-0  h-6  w-6  text-center  border-1.5 flex items-center p-button-icon-only mr-2",
  };

  const chooseOptionsSecondary = {
    className: "text-sm text-primary-copyTextColor underline removePlusIcon",
    label: props.label || "Upload",
  };

  const chooseOptionsTernary = {
    className: " removePlusIcon file-upload-ternary  ",
    label: "",
  };

  const getChooseOptions = () => {
    if (props.variant === VARIANTS.PRIMARY) {
      return chooseOptionsPrimary;
    } else if (props.variant === VARIANTS.SECONDARY) {
      return chooseOptionsSecondary;
    } else if (props.variant === VARIANTS.TERTIARY) {
      return chooseOptionsTernary;
    }
    return chooseOptionsPrimary;
  };

  return (
    <>
      <FileUpload
        name="demo[]"
        accept={props.accept}
        chooseLabel={props.chooseLabel}
        //maxFileSize={1000000}
        headerClassName={props.headerClassName}
        uploadHandler={props.uploadHandler}
        onSelect={props.onSelect}
        onClear={props.onClear}
        onUpload={props.Upload}
        mode="basic"
        auto
        disabled={props?.disabled || props?.isDisabledByAcl}
        chooseOptions={getChooseOptions()}
        //customUpload
        //multiple={true}
      />
      {props.variant === VARIANTS.TERTIARY ? (
        <SimpleToolTip
          variant={INPUT_STYLE_TYPE?.PRIMARY}
          position="top"
          htmlContent={props?.tooltipContent ?? ""}
          target={`.file-upload-${props?.tooltipId}`}
        />
      ) : null}
    </>
  );
};

export default withAuth(FileUploadBox);
